<template>
  <div>
    <!-- Header section -->
    <header class="jumbotron jumbotron-fluid">
      <div class="container">
        <div class="top-left-text">
          <p>Welcome, {{ name }}</p>
        </div>
        <div class="top-right-links">
          <!-- Profile Button -->
          <router-link to="/sponsor_edit_profile">
            <button class="btn btn-profile ml-3">Profile</button>
          </router-link>
          <!-- Logout Button -->
          <button @click="logout" class="btn btn-danger ml-3 logout-btn">Logout</button>
        </div>
        <h1 class="display-4">Sponsor Dashboard</h1>
        <p class="lead">Manage your Influencers effectively.</p>
      </div>
    </header>

    <!-- Flash Messages -->
    <ul class="flashes" v-if="messages.length">
      <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
    </ul>

    <!-- Dashboard -->
    <div class="container">
      <div class="dashboard-section">
        <h2>Sponsor Details</h2>
        <p><strong>Username:</strong> {{ currentUser.username }}</p>
      </div>

      <!-- Export CSV Button -->
      <div class="mt-4">
        <button @click="exportCsv" class="btn btn-primary">
          <i class="fas fa-file-csv mr-1"></i>Export Campaigns to CSV
        </button>
      </div>

      <!-- Campaigns Section -->
      <h3 class="mt-5">Your Campaigns</h3>
      <div class="row">
        <div class="col-md-6 mb-4" v-for="campaign in campaigns" :key="campaign.campaign_id">
          <div class="card">
            <div class="card-body">
              <h5 class="card-title">{{ campaign.name }}</h5>
              <p class="card-text">{{ campaign.description }}</p>
              <p class="card-text">Start Date: {{ formatDate(campaign.start_date) }}</p>
              <p class="card-text">End Date: {{ formatDate(campaign.end_date) }}</p>
              <p class="card-text">Budget: INR{{ campaign.budget }}</p>
              <p class="card-text">Visibility: {{ campaign.visibility }}</p>

              <!-- Ad Requests -->
              <h6 class="mt-3">Ad Requests</h6>
              <ul v-if="campaign.ad_requests.length" class="list-group">
                <li class="list-group-item" v-for="ad_request in campaign.ad_requests" :key="ad_request.ad_request_id">
                  <p>Influencer Username: {{ ad_request.username }}</p>
                  <p>Messages: {{ ad_request.messages }}</p>
                  <p>Requirements: {{ ad_request.requirements }}</p>
                  <p>Payment Amount: INR{{ ad_request.payment_amount }}</p>
                  <p>Payment Details: {{ ad_request.payment_details }}</p>
                  <p>Status: {{ ad_request.status }}</p>
                  <div class="btn-group mt-2">
                    <router-link :to="`/update_ad_request/${ad_request.ad_request_id}`" class="btn btn-sm btn-edit"><i class="fas fa-edit mr-1"></i>Edit</router-link>
                    <button @click="confirmDelete(ad_request.ad_request_id)" class="btn btn-sm btn-delete"><i class="fas fa-trash-alt mr-1"></i>Delete</button>
                  </div>
                </li>
              </ul>
              <p v-else>No ad requests for this campaign.</p>
              <div class="mt-3">
                <router-link :to="`/create_ad_request/${campaign.campaign_id}`" class="btn btn-sm btn-create"><i class="fas fa-plus-circle mr-1"></i>Create Ad Request</router-link>
              </div>
              <div class="mt-3">
                <router-link :to="`/update_campaign/${campaign.campaign_id}`" class="btn btn-sm btn-edit mr-2"><i class="fas fa-edit mr-1"></i>Update Campaign</router-link>
                <button @click="deleteCampaign(campaign.campaign_id)" class="btn btn-sm btn-delete"><i class="fas fa-trash-alt mr-1"></i>Delete Campaign</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Button of Create Campaign -->
      <router-link to="/create_campaign" class="btn btn-success mt-4"><i class="fas fa-plus-circle mr-1"></i>Create New Campaign</router-link>

      <!-- Link to Search Influencers -->
      <div class="dashboard-section">
        <h3 class="mt-5">Search Influencers</h3>
        <router-link to="/search_influencers" class="btn btn-search"><i class="fas fa-search mr-1"></i>Search Influencers</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true; // Include credentials with requests

export default {
  data() {
    return {
      user: { name: '' },
      currentUser: {},
      campaigns: [],
      messages: [], // Flash messages array
      error: null,
    };
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleDateString();
    },
    async fetchCampaigns() {
      try {
        const response = await axios.get('http://localhost:5000/sponsor_dashboard', {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        });
        if (response.data.success) {
          localStorage.setItem('response', JSON.stringify(response.data));
        }
        const data = response.data;
        this.user.name = data.user.username;
        this.currentUser = data.user;
        this.name = this.currentUser.username;
        this.campaigns = response.data.campaigns.map(campaign => ({
          ...campaign,
          ad_requests: campaign.ad_requests.map(ad => ({
            ...ad,
            username: ad.username
          }))
        }));
      } catch (error) {
        console.error('Error fetching campaigns:', error);
      }
    },
    async exportCsv() {
      try {
        const response = await axios.get('http://localhost:5000/export_csv', {
          responseType: 'blob', // Ensure the response is treated as a blob
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          },
        });

        // Create a URL for the CSV file
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', `${this.currentUser.username}_campaigns.csv`); // Set the file name
        document.body.appendChild(link);
        link.click();
        link.remove();
        
        alert('CSV export completed! Your file is downloading.');
      } catch (error) {
        console.error('Error exporting CSV:', error);
        alert('Error exporting CSV. Please try again later.');
      }
    },
    confirmDelete(adRequestId) {
      if (confirm("Are you sure you want to delete this ad request? This action cannot be undone.")) {
        this.deleteAdRequest(adRequestId);
      }
    },
    async deleteAdRequest(adRequestId) {
      try {
        await axios.delete(`http://localhost:5000/delete_ad_request/${adRequestId}`, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.fetchCampaigns();
      } catch (error) {
        console.error('Delete failed', error);
      }
    },
    async deleteCampaign(campaignId) {
      try {
        await axios.delete(`http://localhost:5000/delete_campaign/${campaignId}`, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.fetchCampaigns();
      } catch (error) {
        console.error('Delete failed', error);
      }
    },
    // Logout function
    async logout() {
      try {
        // Show confirmation alert
        alert('You are logging out. You will need to log in again to view the content.');

        // Send logout request to the server
        const response = await axios.post('http://localhost:5000/logout', {}, {
          withCredentials: true
        });

        if (response.status === 200) {
          alert(response.data.message); // Display success message
          // Redirect to home page (or login page)
          this.$router.replace('/'); // Use replace to avoid keeping the logout page in history
        } else {
          alert('Logout failed. Please try again.');
        }
      } catch (error) {
        console.error('Error during logout:', error);
        alert('An error occurred during logout. Please try again.');
      }
    }
  },
  created() {
    this.fetchCampaigns(); // Fetch data when the component is created
  }
};
</script>

<style scoped>
body {
  background-color: lightgray;
  color: #212529;
}
.jumbotron {
  background-color: gold;
  color: #fff;
  text-align: center;
  padding: 100px 0;
  margin-bottom: 0;
  position: relative;
}
.jumbotron h1 {
  font-size: 3.5rem;
  font-weight: bold;
}
.jumbotron p {
  font-size: 1.5rem;
  color: black;
}
.top-right-links {
  position: absolute;
  top: 10px;
  right: 10px;
}
.top-right-links a {
  color: black;
  background-color: transparent !important;
  margin-left: 10px;
}
.top-right-links a:hover {
  text-decoration: none;
}
.top-left-text {
  position: absolute;
  top: 10px;
  left: 10px;
  color: black;
}
.dashboard-section {
  margin-bottom: 30px;
  padding: 20px;
  background-color: white;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}
.dashboard-section h2 {
  font-size: 20px;
  color: blue;
  margin-bottom: 10px;
}
.dashboard-section p {
  font-size: 16px;
  color: #555555;
  margin-bottom: 15px;
}
.card {
  transition: transform 0.2s;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}
.card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.15);
}
.card-title {
  font-size: 1.25rem;
  color: blue;
  margin-bottom: 0.75rem;
}
.card-text {
  font-size: 1rem;
  color: #333;
  margin-bottom: 0.5rem;
}
.btn-create {
  background-color: #28a745;
  color: white;
}
.btn-edit {
  background-color: #007bff;
  color: white;
}
.btn-delete {
  background-color: #dc3545;
  color: white;
}
.btn-search {
  background-color: #17a2b8;
  color: white;
}
.logout-btn {
  background-color: #f44336;
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.logout-btn:hover {
  background-color: #d32f2f;
}
.btn-profile {
  background-color: #007bff;
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.btn-profile:hover {
  background-color: #0056b3;
}
.flashes {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}
.flashes li {
  background-color: #f8d7da;
  color: #721c24;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}
</style>
