<template>
  <div class="view-campaigns">
    <header class="jumbotron jumbotron-fluid">
      <div class="container">
        <h1 class="display-4">Campaigns</h1>
        <p class="lead">Search and view your campaigns effectively.</p>
      </div>
    </header>

    <div class="container">
      <!-- Flash messages -->
      <ul v-if="messages.length" class="flashes">
        <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
      </ul>

      <!-- Search Campaign -->
      <form @submit.prevent="submitForm">
        <div class="form-group">
          <label for="name">{{ form.name.label }}</label>
          <input type="text" v-model="form.name.value" class="form-control" id="name" @input="filterCampaigns" required>
        </div>
        <button type="submit" class="btn btn-primary">{{ form.submit.label }}</button>
      </form>

      <div class="row">
        <div class="col-md-8">
          <ul class="list-group">
            <li v-if="filteredCampaigns.length === 0" class="list-group-item">No campaigns found.</li>
            <li v-for="campaign in filteredCampaigns" :key="campaign.campaign_id" class="list-group-item">
              <h5>{{ campaign.name }}</h5>
              <p>{{ campaign.description }}</p>
              <p><strong>Budget:</strong> INR{{ campaign.budget }}</p>
              <p><strong>Visibility:</strong> {{ campaign.visibility }}</p>
              <p><strong>Start Date:</strong> {{ formatDate(campaign.start_date) }}</p>
              <p><strong>End Date:</strong> {{ formatDate(campaign.end_date) }}</p>
            </li>
          </ul>
        </div>
      </div>
      <!-- Back button -->
      <router-link to="/influencer_dashboard" class="btn btn-secondary mt-3">Back</router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      form: {
        name: {
          label: 'Campaign Name',
          value: ''
        },
        submit: {
          label: 'Search'
        }
      },
      campaigns: [],
      messages: []
    };
  },
  computed: {
    filteredCampaigns() {
      if (!this.form.name.value) {
        return this.campaigns;
      }
      const searchTerm = this.form.name.value.toLowerCase();
      return this.campaigns.filter(campaign => 
        campaign.name.toLowerCase().startsWith(searchTerm)
      );
    }
  },
  methods: {
    async submitForm() {
      try {
        const response = await axios.post('http://127.0.0.1:5000/view_campaigns', {
          name: this.form.name.value
        }, {
          withCredentials: true // Include credentials for session management
        });
        this.campaigns = response.data.campaigns;
        if (this.campaigns.length === 0) {
          this.messages.push('No campaigns found for your search.');
        }
      } catch (error) {
        console.error('Error fetching campaigns:', error);
        this.messages.push('Error fetching campaigns');
      }
    },

    async fetchCampaigns() {
      try {
        const response = await axios.get('http://127.0.0.1:5000/view_campaigns', {
          withCredentials: true
        });
        this.campaigns = response.data.campaigns;
      } catch (error) {
        console.error('Error fetching campaigns:', error);
        this.messages.push('Error fetching campaigns');
      }
    },

    filterCampaigns() {
      // This method is just a placeholder to trigger the computed property
    },

    formatDate(date) {
      return new Date(date).toLocaleDateString();
    }
  },
  mounted() {
    this.fetchCampaigns();
  }
};
</script>

<style scoped>
.view-campaigns {
  padding: 20px;
}
.jumbotron {
  background-color: #007bff;
  color: #fff;
  text-align: center;
  padding: 50px 0;
  margin-bottom: 20px;
}
h1 {
  margin-bottom: 20px;
}
.form-group {
  margin-bottom: 20px;
}
.flashes {
  color: red;
}
.list-group-item {
  margin-bottom: 15px;
}
</style>
