<template>
  <div class="container mt-4">
    <h2><i class="fas fa-edit mr-2"></i>Update Ad Request</h2>
    <button @click="goBack" class="btn btn-secondary mb-3">
      <i class="fas fa-arrow-left mr-2"></i>Back
    </button>
    <form @submit.prevent="updateAdRequest">
      <div class="form-group">
        <label for="username"><i class="fas fa-user mr-2"></i>Influencer Username:</label>
        <input type="text" v-model="form.username" class="form-control" id="username" placeholder="Enter Influencer Username" @input="filterUsers" required>
        <ul v-if="filteredUsers.length" class="suggestions">
          <li v-for="user in filteredUsers" :key="user.id" @click="selectUser(user.username)">{{ user.username }}</li>
        </ul>
      </div>
      <div class="form-group">
        <label for="requirements"><i class="fas fa-tasks mr-2"></i>Requirements:</label>
        <input type="text" v-model="form.requirements" class="form-control" id="requirements" placeholder="Enter Requirements">
      </div>
      <div class="form-group">
        <label for="messages"><i class="fas fa-tasks mr-2"></i>Messages:</label>
        <input type="text" v-model="form.messages" class="form-control" id="messages" placeholder="Enter Messages">
      </div>
      <div class="form-group">
        <label for="payment_amount"><i class="fas fa-INR-sign mr-2"></i>Payment Amount:</label>
        <input type="number" v-model="form.payment_amount" class="form-control" id="payment_amount" placeholder="Enter Payment Amount" required>
      </div>
      <div class="form-group">
        <label for="payment_details"><i class="fas fa-INR-sign mr-2"></i>Payment Details For Influencer:</label>
        <input type="text" v-model="form.payment_details" class="form-control" id="payment_details" placeholder="Payment Details for Influencer">
      </div>
      <div class="form-group">
        <label for="status"><i class="fas fa-check-circle mr-2"></i>Status:</label>
        <select v-model="form.status" class="form-control" id="status" required>
          <option value="pending">Pending</option>
          <option value="approved">Approved</option>
          <option value="rejected">Rejected</option>
        </select>
      </div>
      <button type="submit" class="btn btn-primary"><i class="fas fa-save mr-2"></i>Update Ad Request</button>
    </form>
    <ul v-if="messages.length" class="flashes">
      <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true;

export default {
  data() {
    return {
      messages: [],
      form: {
        username: '',
        requirements: '',
        messages: '',
        payment_amount: '',
        payment_details: '',
        status: 'pending'
      },
      users: [],
      filteredUsers: []
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1); // Navigates back to the previous route
    },
    async fetchAdRequestData() {
      const adRequestId = this.$route.params.id;
      try {
        const response = await axios.get(`http://localhost:5000/ad_request/${adRequestId}`, {
          headers: { 'Content-Type': 'application/json' }
        });
        const data = response.data;
        Object.assign(this.form, data);
      } catch (error) {
        console.error('Error fetching ad request data:', error);
      }
    },
    async fetchUsers() {
      try {
        const response = await axios.get('http://localhost:5000/users', {
          headers: { 'Content-Type': 'application/json' }
        });
        this.users = response.data.filter(user => user.role === 'influencer');
      } catch (error) {
        console.error('Error fetching users:', error);
      }
    },
    filterUsers() {
      this.filteredUsers = this.users.filter(user =>
        user.username.toLowerCase().includes(this.form.username.toLowerCase())
      );
    },
    selectUser(username) {
      this.form.username = username;
      this.filteredUsers = [];
    },
    async updateAdRequest() {
      const adRequestId = this.$route.params.id;
      try {
        const response = await axios.post(`http://localhost:5000/update_ad_request/${adRequestId}`, {
          username: this.form.username,
          requirements: this.form.requirements,
          messages: this.form.messages,
          payment_amount: this.form.payment_amount,
          payment_details: this.form.payment_details,
          status: this.form.status
        }, {
          headers: { 'Content-Type': 'application/json' }
        });
        if (response.data.success) {
          alert(response.data.message);
          this.$router.push('/sponsor_dashboard');
        } else {
          this.messages.push(response.data.message);
        }
      } catch (error) {
        this.messages.push("An error occurred: " + (error.response ? error.response.data.message : error.message));
      }
    }
  },
  mounted() {
    this.fetchAdRequestData();
    this.fetchUsers();
  }
};
</script>

<style scoped>
/* Styles */
body {
  background-color: #f8f9fa;
  font-family: Arial, sans-serif;
}
.container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 50px;
  max-width: 600px;
}
h2 {
  color: blue;
  margin-bottom: 20px;
}
.form-group label {
  font-weight: bold;
}
.suggestions {
  border: 1px solid #ccc;
  max-height: 150px;
  overflow-y: auto;
  background: white;
  position: absolute;
  z-index: 1000;
}
.suggestions li {
  padding: 10px;
  cursor: pointer;
}
.suggestions li:hover {
  background-color: #f1f1f1;
}
.btn-primary {
  background-color: blue;
  border-color: #007bff;
}
.btn-primary:hover {
  background-color: #0056b3;
  border-color: #0056b3;
}
.flashes {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}
.flashes li {
  background-color: #f8d7da;
  color: #721c24;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}
</style>
