<template>
  <div class="container">
    <div class="header">
      <h2>Search Influencers</h2>
      <div>
        <router-link to="/sponsor_dashboard" class="btn btn-primary">Dashboard</router-link>
        <router-link to="/" class="btn btn-primary ml-2">Home</router-link>
      </div>
    </div>

    <form @submit.prevent="searchInfluencers">
      <div class="form-row">
        <div class="col-md-6 mb-3">
          <label for="name" class="form-control-label">Name:</label>
          <input type="text" id="name" v-model="form.name" class="form-control">
        </div>
        <div class="col-md-4 mb-3">
          <label for="reach" class="form-control-label">Reach:</label>
          <input type="number" id="reach" v-model="form.reach" class="form-control">
        </div>
        <div class="col-md-2 mb-3">
          <button type="submit" class="btn btn-primary btn-block">Search</button>
        </div>
      </div>
    </form>

    <div v-if="influencers.length" class="result-container">
      <h3>Search Results</h3>
      <ul class="list-group">
        <li class="list-group-item" v-for="influencer in influencers" :key="influencer.username">
          <strong>Username: {{ influencer.username }}</strong>, Name: {{ influencer.name }}, Reach: {{ influencer.reach }}, Niche: {{ influencer.niche }}
        </li>
      </ul>
    </div>
    <div v-else>
      <div class="alert alert-info mt-4" role="alert">
        No influencers found matching the criteria.
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true;

export default {
  data() {
    return {
      form: {
        name: '',
        reach: ''
      },
      influencers: [] // Initialize with actual influencer data if available
    };
  },
  methods: {
    async searchInfluencers() {
      try {
        const response = await axios.post('http://localhost:5000/search_influencers', this.form, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.influencers = response.data.influencers;
      } catch (error) {
        console.error('Error fetching influencers:', error);
      }
    }
  }
};
</script>

<style>
.container {
  margin-top: 50px;
}
.result-container {
  margin-top: 20px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
</style>
