<template>
  <div class="container animated fadeIn">
    <h1>Edit Profile</h1>

    <div v-if="deleteConfirmation" class="mt-3">
      <p class="delete-link">Are you sure you want to delete your profile?</p>
      <div class="button-group">
        <button @click="confirmDelete" class="btn btn-danger">
          <i class="fas fa-trash"></i> Yes, Delete
        </button>
        <button @click="cancelDelete" class="btn btn-secondary">No, Cancel</button>
      </div>
    </div>

    <form @submit.prevent="saveChanges">
      <div class="form-group">
        <label for="username">New Username:</label>
        <input type="text" id="username" v-model="form.username" class="form-control">
        <p v-if="formErrors.username" class="error-message">{{ formErrors.username }}</p>
        <p v-if="messages.includes('Username taken')" class="error-message">Username taken.</p>
      </div>
      <div class="form-group">
        <label for="password">Password:</label>
        <input type="password" id="password" v-model="form.password" class="form-control">
        <p v-if="formErrors.password" class="error-message">{{ formErrors.password }}</p>
      </div>
      <div class="button-group">
        <button type="submit" class="btn btn-primary">Save Changes</button>
        <router-link to="/sponsor_dashboard" class="btn btn-secondary">Dashboard</router-link>
      </div>
    </form>

    <button @click="initiateDelete" class="btn btn-danger mt-3">Delete Profile</button>

    <ul class="flashes" v-if="messages.length">
      <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true;

export default {
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      formErrors: {
        username: '',
        password: ''
      },
      messages: [],
      deleteConfirmation: false,
    };
  },
  methods: {
    async saveChanges() {
      try {
        const response = await axios.post('http://localhost:5000/sponsor_edit_profile', this.form, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.messages.push(response.data.message);
      } catch (error) {
        if (error.response && error.response.data.message) {
          this.messages.push(error.response.data.message);
        } else {
          this.messages.push("An error occurred: " + (error.response ? error.response.data.message : error.message));
        }
      }
    },

    initiateDelete() {
      this.deleteConfirmation = true;
    },
    cancelDelete() {
      this.deleteConfirmation = false;
    },
    async confirmDelete() {
      try {
        const response = await axios.post('http://localhost:5000/sponsor_edit_profile', {
          delete_confirm: true
        }, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.messages.push(response.data.message);
        this.$router.push('/'); // Redirect to homepage
      } catch (error) {
        this.messages.push("An error occurred: " + (error.response ? error.response.data.message : error.message));
      }
    },
    async fetchProfileData() {
      try {
        const response = await axios.get('http://localhost:5000/sponsor_profile_data');
        this.form.username = response.data.username;
      } catch (error) {
        console.error('Error fetching profile data:', error);
      }
    },
  },
  mounted() {
    this.fetchProfileData();
  }
};
</script>

<style>
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
}
.container {
  max-width: 600px;
  margin: 20px auto;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 8px;
  animation: fadeIn 1s;
}
h1 {
  font-size: 24px;
  color: #333333;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 10px;
}
.form-group {
  margin-bottom: 20px;
}
input[type="text"], input[type="password"] {
  width: 100%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.button-group {
  display: flex;
  justify-content: space-between; /* Equal spacing */
  margin-top: 20px;
}
.btn {
  flex: 1; /* Allow buttons to grow equally */
  margin: 0 10px; /* Add margin for spacing */
  transition: background-color 0.3s ease, transform 0.2s ease; /* Add transition effects */
}
.btn:hover {
  transform: translateY(-2px); /* Subtle lift effect on hover */
}
.btn-danger {
  background-color: #dc3545;
}
.btn-danger:hover {
  background-color: #c82333;
}
.btn-secondary {
  background-color: #6c757d;
}
.btn-secondary:hover {
  background-color: #5a6268;
}
.btn-primary {
  background-color: #007bff;
}
.btn-primary:hover {
  background-color: #0056b3;
}
.error-message {
  color: red;
  font-size: 14px;
  margin-top: 4px;
}
.flashes {
  margin-top: 20px;
  color: red;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
</style>
