<template>
  <div class="login-container">
    <h1>Login</h1>
    <form @submit.prevent="submitForm" class="login-form">
      <div class="form-group">
        <label for="username">Username</label>
        <input
          type="text"
          id="username"
          v-model="username"
          required
          class="form-input"
        />
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input
          type="password"
          id="password"
          v-model="password"
          required
          class="form-input"
        />
      </div>
      <button type="submit" class="submit-button">Login</button>
      <div v-if="errorMessage" class="error">{{ errorMessage }}</div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      password: '',
      errorMessage: ''
    };
  },
  methods: {
    async submitForm() {
      this.errorMessage = ''; // Reset error message before submission

      try {
        const response = await axios.post('http://localhost:5000/login', {
          username: this.username,
          password: this.password
        }, {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
          },
        });

        if (response.data.success) {
          localStorage.setItem('userRole', response.data.role);

          // Check role and approval status
          if (response.data.role === 'admin') {
            this.$router.push('/admin_dashboard');
          } else if (response.data.role === 'influencer') {
            this.$router.push('/influencer_dashboard'); // No approval check for influencers
          } else if (response.data.role === 'sponsor') {
            if (response.data.approved) {
              this.$router.push('/sponsor_dashboard');
            } else {
              this.errorMessage = 'Your sponsor account is not approved yet. Please contact admin.';
            }
          } else {
            this.errorMessage = 'Invalid role';
          }
        } else {
          this.errorMessage = response.data.message || 'Login failed.';
        }
      } catch (error) {
        if (error.response) {
          this.errorMessage = error.response.data.message || 'An error occurred.';
        } else {
          this.errorMessage = 'Network error. Please try again.';
        }
      }
    }
  }
};
</script>

<style scoped>
.login-container {
  max-width: 400px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #555;
}

.form-input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1em;
}

.form-input:focus {
  border-color: #42b983;
  outline: none;
}

.submit-button {
  padding: 10px;
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
}

.submit-button:hover {
  background-color: #36a672;
}

.error {
  color: red;
  text-align: center;
  margin-top: 10px;
}
</style>
