<template>
  <div class="container animated fadeIn">
    <div v-if="deleteConfirmation" class="mt-3">
      <p class="delete-link">Are you sure you want to delete your profile?</p>
      <button @click="confirmDelete" class="btn btn-danger">
        <i class="fas fa-trash-alt mr-2"></i>Yes, Delete
      </button>
      <router-link to="/edit-profile" class="btn btn-secondary ml-2">
        <i class="fas fa-times-circle mr-2"></i>No, Cancel
      </router-link>
    </div>

    <h1 class="animated fadeInDown"><i class="fas fa-user-edit mr-2"></i>Edit Profile</h1>
    <div class="form-group animated fadeIn">
      <label for="niche"><i class="fas fa-bookmark mr-2"></i>Niche:</label>
      <input type="text" id="niche" v-model="form.niche" class="form-control" required>
    </div>
    <div class="form-group animated fadeIn">
      <label for="reach"><i class="fas fa-users mr-2"></i>Reach:</label>
      <input type="number" id="reach" v-model="form.reach" class="form-control" required>
    </div>
    <div class="form-group animated fadeIn">
      <label for="username"><i class="fas fa-user-circle mr-2"></i>New Username:</label>
      <input type="text" id="username" v-model="form.username" class="form-control">
      <p v-if="usernameError" class="error-message">{{ usernameError }}</p>
    </div>
    <div class="form-group animated fadeIn">
      <label for="password"><i class="fas fa-lock mr-2"></i>Password:</label>
      <input type="password" id="password" v-model="form.password" class="form-control">
      <p v-if="passwordError" class="error-message">{{ passwordError }}</p>
    </div>
    <button @click="submitForm" class="btn btn-primary animated fadeIn">
      <i class="fas fa-save mr-2"></i>Save Changes
    </button>
    <router-link to="/influencer_dashboard" class="btn btn-secondary ml-2 animated fadeIn">
      <i class="fas fa-times-circle mr-2"></i>Cancel
    </router-link>

    <a @click.prevent="initiateDelete" class="btn btn-danger ml-2 animated fadeIn">
      <i class="fas fa-trash-alt mr-2"></i>Delete Profile
    </a>

    <ul class="flashes">
      <li v-for="message in messages" :key="message" class="animated fadeIn">{{ message }}</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      form: {
        niche: '',
        reach: '',
        username: '',
        password: ''
      },
      deleteConfirmation: false,
      messages: [],
      usernameError: '',
      passwordError: ''
    };
  },
  methods: {
    async submitForm() {
      // Validate form
      if (this.validateForm()) {
        try {
          const response = await axios.post('http://localhost:5000/edit_profile', {
            niche: this.form.niche,
            reach: this.form.reach,
            username: this.form.username,
            password: this.form.password
          });
          this.messages.push(response.data.message);
          // Fetch updated data and redirect to dashboard
          this.$router.push('/influencer_dashboard');
        } catch (error) {
          this.messages.push(error.response.data.message || 'Error updating profile.');
        }
      }
    },
    validateForm() {
      this.usernameError = this.form.username ? '' : 'Username is required.';
      this.passwordError = this.form.password ? '' : 'Password is required.';
      return !this.usernameError && !this.passwordError;
    },
    initiateDelete() {
      this.deleteConfirmation = true;
    },
    async confirmDelete() {
      try {
        const response = await axios.post('http://localhost:5000/edit_profile', { delete_confirm: true });
        this.messages.push(response.data.message);
        // Redirect or log out
        this.$router.push('/');
      } catch (error) {
        this.messages.push(error.response.data.message || 'Error deleting profile.');
      }
    },
    async fetchProfileData() {
      try {
        const response = await axios.get('http://localhost:5000/influencer_dashboard');
        this.form.niche = response.data.niche;
        this.form.reach = response.data.reach;
        this.form.username = response.data.username; // Adjust if necessary
      } catch (error) {
        this.messages.push('Error fetching profile data.');
      }
    }
  },
  mounted() {
    this.fetchProfileData();
  }
};
</script>

<style>
.container {
  max-width: 600px;
  margin: 50px auto;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 8px;
}
h1 {
  font-size: 24px;
  color: blue;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 10px;
}
.delete-link {
  color: red;
  font-size: 14px;
  text-decoration: none;
  margin-top: 10px;
  display: inline-block;
}
.delete-link:hover {
  text-decoration: underline;
}
.error-message {
  color: red;
  font-size: 14px;
  margin-top: 4px;
}
.ml-2 {
  margin-left: 0.5rem;
}

/* Animations */
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

.fadeIn {
  animation-name: fadeIn;
}

.fadeInLeft {
  animation-name: fadeInLeft;
}

.fadeInRight {
  animation-name: fadeInRight;
}

.fadeInUp {
  animation-name: fadeInUp;
}

.fadeInDown {
  animation-name: fadeInDown;
}
</style>
