<template>
  <div class="influencer-dashboard">
    <header class="jumbotron jumbotron-fluid">
      <div class="container">
        <h1 class="display-4">Influencer Dashboard</h1>
        <p class="lead">Receive your campaigns and ad requests effectively.</p>
      </div>
    </header>
    
    <div class="container">
      <h1>Welcome, {{ influencerName }}!</h1>
      <router-link to="/edit_profile" class="btn btn-warning profile-link animated fadeIn">Profile</router-link>
      
      <!-- Logout Button -->
      <button @click="logout" class="btn btn-danger logout-btn animated fadeIn">Logout</button>

      <div class="dashboard-section" v-if="!loading">
        <h2>Influencer Details</h2>
        <p><strong>Username:</strong> {{ username }}</p>
        <p><strong>Niche:</strong> {{ niche }}</p>
        <p><strong>Reach:</strong> {{ reach }}</p>
      </div>

      <div class="row">
        <div class="col-md-4 mb-4" v-for="(title, index) in cardTitles" :key="index">
          <div class="card animate-fade-in">
            <div class="card-body text-center">
              <h5 class="card-title">{{ title }}</h5>
              <p class="card-text">{{ getCardContent(title) }}</p>
            </div>
          </div>
        </div>
      </div>

      <div class="dashboard-links">
        <h3>Quick Links</h3>
        <ul>
          <li><router-link to="/view_campaigns">View Campaigns</router-link></li>
          <li><router-link :to="'/view_ad_requests/' + username">View Ad Requests</router-link></li>
        </ul>
      </div>

      <ul class="flashes" v-if="messages.length">
        <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      influencerName: '',
      username: '',
      niche: '',
      reach: 0,
      activeCampaigns: 0,
      pendingRequests: 0,
      ongoingNegotiations: 0,
      messages: [],
      loading: true,
      cardTitles: ['Campaigns', 'Ad Requests', 'Negotiations']
    };
  },
  methods: {
    async fetchDashboardData() {
      try {
        const response = await axios.get('http://localhost:5000/influencer_dashboard', {
          withCredentials: true
        });
        
        const data = response.data;
        this.influencerName = data.name || '';
        this.username = data.username || '';
        this.niche = data.niche || '';
        this.reach = data.reach || 0;
        this.activeCampaigns = data.active_campaigns || 0;
        this.pendingRequests = data.pending_requests || 0;
        this.ongoingNegotiations = data.ongoing_negotiations || 0;
        this.messages = data.messages || []; // Assuming messages might come from the server
      } catch (error) {
        console.error('Error fetching dashboard data:', error);
        this.messages.push('Failed to load dashboard data. Please try again later.');
      } finally {
        this.loading = false;
      }
    },

    // Fetch the dynamic content based on the card title
    getCardContent(title) {
      switch (title) {
        case 'Campaigns':
          return `${this.activeCampaigns} Active Campaigns`;
        case 'Ad Requests':
          return `${this.pendingRequests} Pending Requests`;
        case 'Negotiations':
          return `${this.ongoingNegotiations} Ongoing Negotiations`;
        default:
          return '';
      }
    },

    // Logout function
    async logout() {
      try {
        // Show confirmation alert
        alert('You are logging out. You will need to log in again to view the content.');

        // Send logout request to the server
        const response = await axios.post('http://localhost:5000/logout', {}, {
          withCredentials: true
        });

        if (response.status === 200) {
          alert(response.data.message); // Display success message
          // Redirect to home page (or login page)
          this.$router.replace('/'); // Use replace to avoid keeping the logout page in history
        } else {
          alert('Logout failed. Please try again.');
        }
      } catch (error) {
        console.error('Error during logout:', error);
        alert('An error occurred during logout. Please try again.');
      }
    }
  },
  mounted() {
    this.fetchDashboardData();
  }
};
</script>

<style scoped>
.influencer-dashboard {
  padding: 20px;
  animation: fadeIn 1s ease-in-out;
}

.jumbotron {
  background-color: #007bff;
  color: #fff;
  text-align: center;
  padding: 100px 0;
  margin-bottom: 0;
  animation: bounceInDown 1s;
}

.jumbotron h1 {
  font-size: 3.5rem;
  font-weight: bold;
  animation: fadeInUp 1s;
}

.jumbotron p {
  font-size: 1.5rem;
  animation: fadeIn 1s;
}

.dashboard-section {
  margin-bottom: 30px;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 8px;
  animation: fadeIn 1s ease-in-out;
}

.dashboard-section h2 {
  font-size: 20px;
  color: blue;
  margin-bottom: 10px;
  animation: fadeInUp 1s;
}

.dashboard-section p {
  font-size: 16px;
  color: #555555;
  animation: fadeIn 1s;
}

.flashes {
  margin-top: 20px;
  color: red;
  animation: fadeIn 1s;
}

.profile-link {
  display: block;
  margin-top: 20px;
  animation: fadeIn 1s;
}

/* Logout button */
.logout-btn {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.logout-btn:hover {
  background-color: #c82333;
}

/* Animations */
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

.bounceInDown {
  animation-name: bounceInDown;
}

@keyframes bounceInDown {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }

  from {
    opacity: 0;
    transform: translate3d(0, -3000px, 0);
  }

  60% {
    opacity: 1;
    transform: translate3d(0, 25px, 0);
  }

  75% {
    transform: translate3d(0, -10px, 0);
  }

  90% {
    transform: translate3d(0, 5px, 0);
  }

  to {
    transform: none;
  }
}

.fadeIn {
  animation-name: fadeIn;
}

.fadeInLeft {
  animation-name: fadeInLeft;
}

.fadeInRight {
  animation-name: fadeInRight;
}

.fadeInUp {
  animation-name: fadeInUp;
}
</style>
