// router.js
import { createRouter, createWebHistory } from 'vue-router';
import HomeView from './views/HomeView.vue';
import RegisterView from './views/RegisterView.vue';
import LoginView from './views/LoginView.vue';
import AdminView from './views/AdminView.vue';
import SponsorDashboard from './views/SponsorDashboard.vue';
import InfluencerDashboard from './views/InfluencerDashboard.vue';
import CreateCampaign from './views/CreateCampaign.vue';
import CreateAdRequest from './views/CreateAdRequest.vue';

import EditProfile from './views/EditProfile.vue';
import ViewCampaign from './views/ViewCampaign.vue';
import ViewAdRequest from './views/ViewAdRequest.vue';
import SearchInfluencers from './views/SearchInfluencers.vue';

import SponsorEditProfile from './views/SponsorEditProfile.vue';
import UpdateCampaign from './views/UpdateCampaign.vue';
import UpdateAdRequest from './views/UpdateAdRequest.vue';
//import LogoutView from './views/LogoutView.vue';

// Define routes
const routes = [
  { path: '/' , component: HomeView },
  { path: '/register', component: RegisterView },
  { path: '/login', component: LoginView },
  { path: '/admin_dashboard', component: AdminView },
  { path: '/sponsor_dashboard', component: SponsorDashboard },
  { path: '/influencer_dashboard', component: InfluencerDashboard },
  { path: '/create_campaign', component: CreateCampaign },
  { path: '/create_ad_request/:id', component: CreateAdRequest },
  { path: '/edit_profile', component: EditProfile },
  { path: '/view_campaigns', component: ViewCampaign },
  { path: '/view_ad_requests/:username', component: ViewAdRequest },
  
  { path: '/sponsor_edit_profile', component: SponsorEditProfile },
  { path: '/update_campaign/:id', component: UpdateCampaign },
  { path: '/update_ad_request/:id', component: UpdateAdRequest },
  { path: '/delete_ad_request/:id', component: SponsorDashboard },
  { path: '/delete_campaign/:id', component: SponsorDashboard },
  { path: '/search_influencers', component: SearchInfluencers },
  //{ path: '/logout', component: LogoutView },
];

// Create router instance
const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; // Export the router instance
