<template>
  <div class="container mt-4">
    <h2><i class="fas fa-bullhorn mr-2"></i>Create Campaign</h2>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="name"><i class="fas fa-file-alt mr-2"></i>Campaign Name:</label>
        <input v-model="form.name" class="form-control" id="name" placeholder="Enter Campaign Name" required />
      </div>
      <div class="form-group">
        <label for="description"><i class="fas fa-info-circle mr-2"></i>Description:</label>
        <textarea v-model="form.description" class="form-control" id="description" placeholder="Enter Description" required></textarea>
      </div>
      <div class="form-group">
        <label for="start_date"><i class="fas fa-calendar-alt mr-2"></i>Start Date:</label>
        <input type="date" v-model="form.start_date" class="form-control" id="start_date" required />
      </div>
      <div class="form-group">
        <label for="end_date"><i class="fas fa-calendar-alt mr-2"></i>End Date:</label>
        <input type="date" v-model="form.end_date" class="form-control" id="end_date" required />
      </div>
      <div class="form-group">
        <label for="budget"><i class="fas fa-inr-sign mr-2"></i>Budget:</label>
        <input v-model="form.budget" type="number" class="form-control" id="budget" placeholder="Enter Budget" required />
      </div>
      <div class="form-group">
        <label for="visibility"><i class="fas fa-eye mr-2"></i>Visibility:</label>
        <select v-model="form.visibility" class="form-control" id="visibility">
          <option value="public">Public</option>
          <option value="private">Private</option>
        </select>
      </div>
      <div class="d-flex justify-content-between">
        <button type="submit" class="btn btn-primary btn-same-size"><i class="fas fa-plus-circle mr-2"></i>Create Campaign</button>
        <router-link to="/sponsor_dashboard" class="btn btn-secondary btn-same-size btn-back">
          <i class="fas fa-arrow-left mr-2"></i>Back
        </router-link>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

axios.defaults.withCredentials = true;

export default {
  data() {
    return {
      form: {
        name: '',
        description: '',
        start_date: '',
        end_date: '',
        budget: '',
        visibility: 'public',
      },
      error: null,
    };
  },
  methods: {
    async submitForm() {
      try {
        const response = await axios.post('http://localhost:5000/create_campaign', {
          name: this.form.name,
          description: this.form.description,
          start_date: this.form.start_date,
          end_date: this.form.end_date,
          budget: this.form.budget,
          visibility: this.form.visibility,
        }, {
          headers: {
            'Content-Type': 'application/json', // Specify the content type
          }
        });

        console.log('Response data:', response.data);

        if (response.data.success) {
          localStorage.setItem('response', JSON.stringify(response.data));
          alert(response.data.message);
          this.clearForm();
          console.log('Redirecting to sponsor dashboard');
          this.$router.push('/sponsor_dashboard');
        } else {
          this.error = "Failed to create the campaign.";
        }
      } catch (error) {
        this.error = "An error occurred: " + (error.response ? error.response.data.message : error.message);
        alert(this.error);
      }
    },
    clearForm() {
      this.form = {
        name: '',
        description: '',
        start_date: '',
        end_date: '',
        budget: '',
        visibility: 'public',
      };
      this.error = null;
    },
  },
};

</script>

<style scoped>
body {
  background-color: #f8f9fa;
  font-family: Arial, sans-serif;
}
.container {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 50px;
  max-width: 600px;
  animation: fadeIn 1s ease-in-out;
}
h2 {
  color: blue;
  margin-bottom: 20px;
}
.form-group label {
  font-weight: bold;
}
.btn-primary, .btn-secondary {
  background-color: blue;
  border-color: #007bff;
  width: calc(50% - 5px);
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.btn-primary:hover, .btn-secondary:hover {
  background-color: #0056b3;
  border-color: #0056b3;
  transform: scale(1.05);
}
.btn-same-size {
  margin-bottom: 10px;
}
.btn-back {
  background-color: grey;
  border-color: grey;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
</style>
