<template>
  <div class="dashboard-container">
    <nav class="navbar navbar-expand-md navbar-light">
      <div class="container">
        <a class="navbar-brand" href="#">
          <i class="fas fa-chart-line mr-2"></i>Admin Dashboard
        </a>
        <!-- Logout Button -->
        <button @click="logout" class="btn btn-danger ml-auto logout-btn">Logout</button>
      </div>
    </nav>

    <div class="container dashboard-content">
      <div v-if="messages.length" class="flashes">
        <ul>
          <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
        </ul>
      </div>

      <h2>Welcome, {{ user.username }}!</h2>

      <div class="row">
        <div class="col-md-6">
          <h3><i class="fas fa-chart-bar mr-2 stats-icon"></i>Statistics</h3>
          <p><i class="fas fa-user mr-1"></i>Active Users: {{ activeUsersCount }}</p>
          <p><i class="fas fa-bullhorn mr-1"></i>Public Campaigns: {{ publicCampaignsCount }}</p>
          <p><i class="fas fa-lock mr-1"></i>Private Campaigns: {{ privateCampaignsCount }}</p>
          <p><i class="fas fa-ad mr-1"></i>Ad Requests: {{ adRequestsCount }}</p>
          <p><i class="fas fa-flag mr-1"></i>Flagged Users: {{ flaggedUsersCount }}</p>
          <p><i class="fas fa-flag mr-1"></i>Flagged Campaigns: {{ flaggedCampaignsCount }}</p>

          <div class="histogram-container">
            <h3 class="histogram-title"><i class="fas fa-chart-line mr-2"></i>Histogram</h3>
            <div v-for="(value, label) in histogramData" :key="label">
              <p>{{ label }}</p>
              <div class="histogram-bar" :style="{ width: (value / maxValue) * 100 + '%' }"></div>
            </div>
          </div>
        </div>

        <div class="col-md-6">
          <h3><i class="fas fa-flag mr-2 flag-icon"></i>Flag Users or Campaigns</h3>
          <form @submit.prevent="flag">
            <div class="form-group">
              <label for="username"><i class="fas fa-user mr-1"></i>Username to Flag:</label>
              <input type="text" class="form-control" v-model="flagUsername" placeholder="Enter Username" />
            </div>
            <div class="form-group">
              <label for="name"><i class="fas fa-bullhorn mr-1"></i>Campaign Name to Flag:</label>
              <input type="text" class="form-control" v-model="flagCampaignName" placeholder="Enter Campaign Name" />
            </div>
            <button type="submit" class="btn btn-primary">Flag</button>
          </form>
        </div>
      </div>

      <hr />

      <div class="row">
        <div class="col-md-6">
          <h3><i class="fas fa-bullhorn mr-2 campaigns-icon"></i>Campaigns</h3>
          <ul>
            <li v-for="(campaign, index) in campaigns" :key="index">{{ campaign }}</li>
          </ul>
        </div>

        <div class="col-md-6">
          <h3><i class="fas fa-ad mr-2 requests-icon"></i>Ad Requests</h3>
          <ul>
            <li v-for="(adRequest, index) in adRequests" :key="index">{{ adRequest }}</li>
          </ul>
        </div>
      </div>

      <hr />

      <div class="row">
        <div class="col-md-12">
          <h3><i class="fas fa-user-check mr-2"></i>Unapproved Sponsors</h3>
          <ul>
            <li v-for="(sponsor, index) in unapprovedSponsors" :key="index" class="sponsor-item">
              {{ sponsor }}
              <button @click="approveSponsor(sponsor)" class="btn btn-success btn-sm approve-button">Approve</button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true; // Include credentials with requests

export default {
  data() {
    return {
      user: { username: '' },
      messages: [],
      activeUsersCount: 0,
      publicCampaignsCount: 0,
      privateCampaignsCount: 0,
      adRequestsCount: 0,
      flaggedUsersCount: 0,
      flaggedCampaignsCount: 0,
      histogramData: {},
      maxValue: 0,
      flagUsername: '',
      flagCampaignName: '',
      campaigns: [],
      adRequests: [],
      unapprovedSponsors: [] // New data property for unapproved sponsors
    };
  },
  methods: {
    async fetchData() {
      try {
        const response = await axios.get('http://localhost:5000/admin_dashboard', {
          headers: {
            'Content-Type': 'application/json' // Specify the content type
          }
        });
        console.log('Response data:', response.data);
        const data = response.data;
        this.user.username = data.user;
        this.activeUsersCount = data.active_users_count;
        this.publicCampaignsCount = data.public_campaigns_count;
        this.privateCampaignsCount = data.private_campaigns_count;
        this.adRequestsCount = data.ad_requests_count;
        this.flaggedUsersCount = data.flagged_users_count;
        this.flaggedCampaignsCount = data.flagged_campaigns_count;
        this.histogramData = Object.fromEntries(data.histogram_data);
        this.maxValue = data.maxi;
        this.campaigns = data.campaigns;
        this.adRequests = data.ad_requests;
        this.unapprovedSponsors = data.unapproved_sponsors; // Get unapproved sponsors
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },

    async flag() {
      try {
        const response = await axios.post('http://localhost:5000/admin_dashboard', {
          username: this.flagUsername,
          name: this.flagCampaignName
        }, {
          headers: {
            'Content-Type': 'application/json' // Specify the content type
          }
        });
        console.log('Response data:', response.data);
        this.messages.push(response.data.message);
        this.flagUsername = '';
        this.flagCampaignName = '';
        this.fetchData(); // Refresh data after flagging
      } catch (error) {
        console.error('Error flagging:', error);
        const errorMessage = error.response?.data?.message || 'An error occurred';
        this.messages.push(errorMessage);
      }
    },

    async approveSponsor(sponsor) {
      try {
        const response = await axios.post('http://localhost:5000/admin_dashboard', {
          approve_sponsor: sponsor // Send the sponsor's username for approval
        }, {
          headers: {
            'Content-Type': 'application/json' // Specify the content type
          }
        });
        console.log('Approval response data:', response.data);
        this.messages.push(response.data.message);
        this.fetchData(); // Refresh data after approval
      } catch (error) {
        console.error('Error approving sponsor:', error);
        const errorMessage = error.response?.data?.message || 'An error occurred';
        this.messages.push(errorMessage);
      }
    },

    // Logout function
    async logout() {
      try {
        // Show confirmation alert
        alert('You are logging out. You will need to log in again to view the content.');

        // Send logout request to the server
        const response = await axios.post('http://localhost:5000/logout', {}, {
          withCredentials: true
        });

        if (response.status === 200) {
          alert(response.data.message); // Display success message
          // Redirect to login page (or home page)
          this.$router.replace('/login'); // Use replace to avoid keeping the logout page in history
        } else {
          alert('Logout failed. Please try again.');
        }
      } catch (error) {
        console.error('Error during logout:', error);
        alert('An error occurred during logout. Please try again.');
      }
    }
  },
  created() {
    this.fetchData(); // Fetch data when the component is created
  }
};
</script>

<style scoped>
.dashboard-container {
  background-color: LightGray; 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: fadeIn 1s;
}

.dashboard-content {
  background-color: lavender; 
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
  margin-top: 20px;
  width: 80%; /* Adjust width as needed */
  max-width: 900px; /* Set a maximum width */
  animation: slideIn 0.5s;
}

.histogram-container {
  margin-top: 20px;
}

.histogram-bar {
  display: block;
  height: 20px;
  background-color: green; 
  margin-bottom: 5px;
  border-radius: 4px;
  animation: growBar 1s ease-out;
}

.flashes {
  color: red;
  margin-bottom: 20px;
}

.sponsor-item {
  margin-bottom: 15px; /* Space between each unapproved sponsor */
}

.approve-button {
  width: 100px; /* Fixed width for uniform button size */
  transition: background-color 0.3s, transform 0.3s; /* Animation for hover effect */
}

.approve-button:hover {
  background-color: #38c172; /* Change color on hover */
  transform: scale(1.05); /* Slightly enlarge on hover */
}

/* Styling for logout button */
.logout-btn {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  margin-left: auto;
  margin-right: 10px;
  border-radius: 4px;
}

.logout-btn:hover {
  background-color: #d32f2f;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideIn {
  from { transform: translateY(30px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes growBar {
  from { width: 0; }
  to { width: 100%; }
}
</style>
