<template>
  <div class="container mt-4">
    <h2>Update Campaign</h2>
    <ul v-if="messages.length" class="flashes">
      <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
    </ul>
    <form @submit.prevent="updateCampaign">
      <div class="form-group">
        <label for="name">{{ form.name.label }}</label>
        <input type="text" v-model="form.name.value" class="form-control" id="name" required>
      </div>
      <div class="form-group">
        <label for="description">{{ form.description.label }}</label>
        <textarea v-model="form.description.value" class="form-control" id="description"></textarea>
      </div>
      <div class="form-group">
        <label for="start_date">{{ form.start_date.label }}</label>
        <input type="date" v-model="form.start_date.value" class="form-control" id="start_date" required>
      </div>
      <div class="form-group">
        <label for="end_date">{{ form.end_date.label }}</label>
        <input type="date" v-model="form.end_date.value" class="form-control" id="end_date" required>
      </div>
      <div class="form-group">
        <label for="budget">{{ form.budget.label }}</label>
        <input type="number" v-model="form.budget.value" class="form-control" id="budget" required>
      </div>
      <div class="form-group">
        <label for="visibility">{{ form.visibility.label }}</label>
        <select v-model="form.visibility.value" class="form-control" id="visibility" required>
          <option value="public">Public</option>
          <option value="private">Private</option>
        </select>
      </div>
      <button type="submit" class="btn btn-primary">Update Campaign</button>
    </form>
    <a href="/sponsor_dashboard" class="btn btn-secondary mt-3">Back</a>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true; // Include credentials with requests

export default {
  data() {
    return {
      messages: [], // Populate with flash messages if needed
      form: {
        name: { label: 'Campaign Name', value: '' },
        description: { label: 'Description', value: '' },
        start_date: { label: 'Start Date', value: '' },
        end_date: { label: 'End Date', value: '' },
        budget: { label: 'Budget', value: '' },
        visibility: { label: 'Visibility', value: '' },
      },
    };
  },
  methods: {
    async fetchCampaignData() {
      try {
        const campaignId = this.$route.params.id; // Assuming the campaign ID is passed via route params
        const response = await axios.get(`http://localhost:5000/campaign/${campaignId}`, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        const data = response.data;
        this.form.name.value = data.name;
        this.form.description.value = data.description;
        this.form.start_date.value = data.start_date;
        this.form.end_date.value = data.end_date;
        this.form.budget.value = data.budget;
        this.form.visibility.value = data.visibility;
      } catch (error) {
        console.error('Error fetching campaign data:', error);
      }
    },
    async updateCampaign() {
      try {
        const campaignId = this.$route.params.id; // Use the campaign ID from the route params
        const response = await axios.post(`http://localhost:5000/update_campaign/${campaignId}`, {
          name: this.form.name.value,
          description: this.form.description.value,
          start_date: this.form.start_date.value,
          end_date: this.form.end_date.value,
          budget: this.form.budget.value,
          visibility: this.form.visibility.value,
        }, {
          headers: {
            'Content-Type': 'application/json',
          }
        });
        if (response.data.success) {
          alert(response.data.message);
          this.$router.push('/sponsor_dashboard');
        } else {
          this.messages.push(response.data.message);
        }
      } catch (error) {
        this.messages.push("An error occurred: " + (error.response ? error.response.data.message : error.message));
      }
    },
  },
  mounted() {
    this.fetchCampaignData(); // Load the campaign data when the component is created
    this.messages = this.$route.params.messages || [];
  },
};
</script>


<style scoped>
.container {
  margin-top: 50px;
  max-width: 600px;
}
h2 {
  margin-bottom: 20px;
}
.form-group label {
  font-weight: bold;
}
</style>
