<template>
  <div class="register-container">
    <h1>Register</h1>
    <form @submit.prevent="registerUser" class="register-form">
      <div class="form-group">
        <label for="username">Username:</label>
        <input type="text" v-model="username" required class="form-input" />
      </div>
      <div class="form-group">
        <label for="password">Password:</label>
        <input type="password" v-model="password" required class="form-input" />
      </div>
      <div class="form-group">
        <label for="role">Role:</label>
        <select v-model="role" required class="form-input">
          <option value="influencer">Influencer</option>
          <option value="sponsor">Sponsor</option>
        </select>
      </div>
      <div v-if="role === 'influencer'" class="form-group">
        <label for="reach">Reach:</label>
        <input type="number" v-model="reach" required class="form-input" />
      </div>
      <div v-if="role === 'influencer'" class="form-group">
        <label for="niche">Niche:</label>
        <input type="text" v-model="niche" required class="form-input" />
      </div>
      <div class="form-group">
        <label for="name">Name:</label>
        <input type="text" v-model="name" required class="form-input" />
      </div>
      <div class="form-group">
        <label for="email">Email:</label>
        <input type="email" v-model="email" required class="form-input" />
      </div>
      <div class="form-group">
        <label for="phone_number">Phone Number:</label>
        <input type="text" v-model="phone_number" required class="form-input" />
      </div>
      <div class="form-group">
        <label for="preferred_reminder_time">Reminder Time:</label>
        <input type="time" v-model="preferred_reminder_time" required class="form-input" />
      </div>
      <button type="submit" class="submit-button">Register</button>
      <p v-if="error" class="error-message">{{ error }}</p>
      <p v-if="role === 'sponsor'" class="approval-message">
        Note: Sponsors will need admin approval before logging in.
      </p>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      password: '',
      role: 'influencer', // Default role
      reach: null,
      niche: '',
      name: '',
      email: '',
      phone_number: '', // New data property for phone number
      preferred_reminder_time: '18:00', // Default value
      error: null,
    };
  },
  methods: {
    async registerUser() {
      try {
        const response = await axios.post('http://localhost:5000/register', {
          username: this.username,
          password: this.password,
          role: this.role,
          reach: this.role === 'influencer' ? this.reach : null,
          niche: this.role === 'influencer' ? this.niche : null,
          name: this.name,
          email: this.email,
          phone_number: this.phone_number, // Include phone number in the request
          preferred_reminder_time: this.preferred_reminder_time,
        });

        alert(response.data.message); // Show success message
        // this.$router.push('/login'); // Redirect to login page
      } catch (err) {
        this.error = "An error occurred"; // Show error message if any
      }
    },
  },
};
</script>

<style scoped>
.register-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.register-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #555;
}

.form-input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1em;
}

.form-input:focus {
  border-color: #42b983;
  outline: none;
}

.submit-button {
  padding: 10px;
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
}

.submit-button:hover {
  background-color: #36a672;
}

.error-message {
  color: red;
  text-align: center;
  margin-top: 10px;
}

.approval-message {
  color: #ff9800; /* Color for approval message */
  text-align: center;
  margin-top: 10px;
}
</style>
