<template>
  <div id="app">
    <nav>
      <div class="nav-logo">
        <img :src="require('@/assets/image.jpeg')" alt="HANDSHAKE Logo" class="logo animated-logo" />
        <span class="logo-text animated-text">HANDSHAKE</span>
      </div>
      <div class="nav-links">
        <router-link to="/" class="animated-link">Home</router-link>
        <router-link to="/login" class="animated-link">Login</router-link>
        <router-link to="/register" class="animated-link">Register</router-link>
      </div>
    </nav>
    <router-view></router-view> <!-- This renders the matched component -->
    

    <footer class="footer">
      <div class="container">
      <button ref="installButton" style="display: none;">Install App</button> <!-- Initially hidden -->
      <p class="colorful-text">&copy; 2024 Parashmani Site.</p>
      </div>
    </footer>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';

export default {
  name: 'App',
  setup() {
    const installButton = ref(null);
    let deferredPrompt;

    onMounted(() => {
      window.addEventListener('beforeinstallprompt', (e) => {
        e.preventDefault(); // Prevent the mini info bar from appearing
        deferredPrompt = e; // Stash the event so it can be triggered later
        
        // Will Show the install button
        installButton.value.style.display = 'block';
      });

      // To Ensure the install button is clicked
      installButton.value?.addEventListener('click', () => {
        if (deferredPrompt) {
          deferredPrompt.prompt(); // Will Show the install prompt
          deferredPrompt.userChoice.then((choiceResult) => {
            if (choiceResult.outcome === 'accepted') {
              console.log('User accepted the A2HS prompt');
            } else {
              console.log('User dismissed the A2HS prompt');
            }
            deferredPrompt = null; // Will Clear the deferredPrompt variable
            installButton.value.style.display = 'none'; // the button after prompt will be hidden
          });
        }
      });
    });

    return {
      installButton
    };
  }
}
</script>

