<template>
  <div>
    <!-- Navigation Bar -->
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <!-- Hamburger Button for Mobile Screens -->
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      
      <!-- Navbar Links -->
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link class="nav-link" to="/influencer_dashboard">Dashboard</router-link>
          </li>
        </ul>
      </div>
    </nav>

    <!-- Main Content -->
    <div class="container mt-4">
      <h2 class="mb-4">Ad Requests</h2>
      
      <ul class="flashes" v-if="messages.length">
        <li v-for="(message, index) in messages" :key="index">{{ message }}</li>
      </ul>

      <!-- Responsive Table -->
      <div class="table-responsive">
        <table class="table table-bordered table-hover">
          <thead class="thead-dark">
            <tr>
              <th scope="col">Campaign</th>
              <th scope="col">Messages</th>
              <th scope="col">Requirements</th>
              <th scope="col">Payment Amount</th>
              <th scope="col">Status</th>
              <th scope="col">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="ad_request in adRequests" :key="ad_request.ad_request_id">
              <td>{{ ad_request.campaign.name }}</td>
              <td>{{ ad_request.messages }}</td>
              <td>{{ ad_request.requirements }}</td>
              <td>INR {{ ad_request.payment_amount }}</td>
              <td>{{ ad_request.status }}</td>
              <td>
                <button @click="openPaymentForm(ad_request.ad_request_id)" class="btn btn-success btn-sm">Accept</button>
                <button @click="rejectAdRequest(ad_request.ad_request_id)" class="btn btn-danger btn-sm">Reject</button>
                <button @click="openNegotiationForm(ad_request.ad_request_id)" class="btn btn-primary btn-sm">Negotiate</button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- Payment Form -->
    <div v-if="showPaymentForm" class="container mt-4">
      <div class="card">
        <div class="card-header bg-primary text-white">
          <h2>Payment Details</h2>
        </div>
        <div class="card-body">
          <form @submit.prevent="acceptAdRequest(currentAdRequestId)">
            <div class="form-group">
              <label for="paymentDetails">Payment Details</label>
              <input type="text" id="paymentDetails" v-model="paymentDetails" class="form-control" required />
            </div>
            <div class="form-group d-flex justify-content-between">
              <button type="submit" class="btn btn-primary btn-lg">Submit</button>
              <button @click="cancelPaymentForm" class="btn btn-secondary btn-lg">Cancel</button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <!-- Negotiation Form -->
    <div v-if="showNegotiationForm" class="container mt-4">
      <div class="card">
        <div class="card-header bg-primary text-white">
          <h2>Negotiate Ad Request</h2>
        </div>
        <div class="card-body">
          <form @submit.prevent="negotiateAdRequest(currentAdRequestId)">
            <div class="form-group">
              <label for="messages">Messages</label>
              <input type="text" id="messages" v-model="negotiationMessages" class="form-control" required />
            </div>
            <div class="form-group d-flex justify-content-between">
              <button type="submit" class="btn btn-primary btn-lg">Submit</button>
              <button @click="cancelNegotiationForm" class="btn btn-secondary btn-lg">Cancel</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
axios.defaults.withCredentials = true;

export default {
  data() {
    return {
      messages: [],
      adRequests: [],
      showPaymentForm: false,
      showNegotiationForm: false,
      currentAdRequestId: null,
      paymentDetails: '',
      negotiationMessages: ''
    };
  },
  methods: {
    async fetchAdRequests() {
      const username = this.$route.params.username;
      console.log('Fetching ad requests for username:', username);
      if (!username) {
        this.messages.push('Username is not defined.');
        return;
      }
      try {
        const response = await axios.get(`http://127.0.0.1:5000/view_ad_requests/${username}`, {
          headers: { 'Accept': 'application/json' }
        });
        console.log('Response data:', response.data);
        if (response.data.success) {
          this.adRequests = response.data.ad_requests;
        } else {
          this.messages.push(response.data.message);
        }
      } catch (error) {
        console.error('Error fetching ad requests:', error);
        this.messages.push('Failed to load ad requests.');
      }
    },
    openPaymentForm(adRequestId) {
      this.currentAdRequestId = adRequestId;
      this.showPaymentForm = true;
      this.showNegotiationForm = false;
    },
    cancelPaymentForm() {
      this.currentAdRequestId = null;
      this.showPaymentForm = false;
      this.paymentDetails = '';
    },
    async acceptAdRequest(adRequestId) {
      try {
        const response = await axios.post(`http://127.0.0.1:5000/accept_ad_request/${adRequestId}`, {
          payment_details: this.paymentDetails
        }, {
          headers: { 'Accept': 'application/json' }
        });
        this.messages.push(response.data.message);
        this.fetchAdRequests();
        this.cancelPaymentForm();
      } catch (error) {
        console.error('Error accepting ad request:', error);
        this.messages.push('Failed to accept ad request.');
      }
    },
    openNegotiationForm(adRequestId) {
      this.currentAdRequestId = adRequestId;
      this.showNegotiationForm = true;
      this.showPaymentForm = false;
    },
    cancelNegotiationForm() {
      this.currentAdRequestId = null;
      this.showNegotiationForm = false;
      this.negotiationMessages = '';
    },
    async negotiateAdRequest(adRequestId) {
      try {
        const response = await axios.post(`http://127.0.0.1:5000/negotiate_ad_request/${adRequestId}`, {
          messages: this.negotiationMessages
        }, {
          headers: { 'Accept': 'application/json' }
        });
        this.messages.push(response.data.message);
        this.fetchAdRequests();
        this.cancelNegotiationForm();
      } catch (error) {
        console.error('Error negotiating ad request:', error);
        this.messages.push('Failed to negotiate ad request.');
      }
    },
    async rejectAdRequest(adRequestId) {
      try {
        const response = await axios.post(`http://127.0.0.1:5000/reject_ad_request/${adRequestId}`, {}, {
          headers: { 'Accept': 'application/json' }
        });
        this.messages.push(response.data.message);
        this.fetchAdRequests();
      } catch (error) {
        console.error('Error rejecting ad request:', error);
        this.messages.push('Failed to reject ad request.');
      }
    }
  },
  mounted() {
    this.fetchAdRequests();
  }
};
</script>

<style scoped>
.navbar {
  margin-bottom: 20px;
}
.container {
  margin-top: 20px;
}
.table {
  margin-top: 20px;
}
.table-hover tbody tr:hover {
  background-color: #f1f1f1;
}
.btn {
  margin-right: 5px;
}
.flashes {
  color: red;
}
.card {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}
.card-header {
  background-color: #007bff;
  color: white;
  font-size: 1.25rem;
}
.btn-lg {
  width: 100px;
}

/* Make the table responsive */
.table-responsive {
  overflow-x: auto;
}

/* Add custom margin to prevent overlapping on smaller screens */
@media (max-width: 768px) {
  .btn-lg {
    width: auto;
  }
}
</style>
